/* src/components/Navbar.css */

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0f0f0f; /* Almost black */
  padding: 0.75rem 1.5rem;
  color: #e0f2e9;
  position: sticky;
  top: 0;
  z-index: 10;
}

.navbar-logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #1db954; /* Dark Green (Spotify-style) */
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.store-name {
  font-family: 'Segoe UI', sans-serif;
  color: #e6ffe6;
}

.menu-toggle {
  display: none;
  font-size: 2rem;
  color: #e0f2e9;
  cursor: pointer;
}

.navbar-links {
  display: flex;
  gap: 1.5rem;
  list-style: none;
}

.navbar-links li a {
  color: #e0f2e9;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s;
}

.navbar-links li a:hover {
  color: #1db954;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }

  .navbar-links {
    display: none;
    flex-direction: column;
    background-color: #0f0f0f;
    position: absolute;
    top: 60px;
    right: 20px;
    width: 160px;
    padding: 1rem;
    border: 1px solid #1db954;
    border-radius: 10px;
  }

  .navbar-links.open {
    display: flex;
  }
}
